::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.ss-input {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  width: 100%;
  height: 2.4rem;
  font-size: 0.9rem;
  padding: 0 0.6rem;
  color: #26262F;
  border: 0.1rem solid #9D9DA3;
  border-radius: 0.2rem;
  background: #FFFFFF;
  transition: border-color 200ms ease, background 200ms ease;
  vertical-align: middle;
}

.ss-input::before, .ss-input::after {
  outline: none;
}

.ss-input::placeholder {
  color: rgba(38, 38, 47, 0.3);
}

.ss-input::-webkit-input-placeholder {
  color: rgba(38, 38, 47, 0.3);
}

.ss-input::-moz-placeholder {
  color: rgba(38, 38, 47, 0.3);
}

.ss-input::-ms-input-placeholder {
  color: rgba(38, 38, 47, 0.3);
}

.ss-input:focus:not([tabindex='-1']) {
  border-color: #26262F;
  background: #FFFFFF;
  outline-offset: 0.15rem;
  outline: rgba(102, 166, 238, 0.5) solid 0.15rem;
}

.ss-input-scale-none {
  height: 2.4rem;
  font-size: 0.9rem;
